import React, { Component } from 'react';
import './home.css'; 
import HomeHeader from './HomeHeader.js';
import HomeFooter from './HomeFooter.js';

var $ = require('jquery');

class PicText extends Component {
    componentDidMount() {
    }

    viewDetail(href) {
        window.location.href = href;
    }

    render() {
        var partClassName = "news-middle-two-content";
        if (3 == this.props.type) {
   	        partClassName = "news-middle-three-content";
        }
        return (
            <section className={partClassName}>
                <img className="news-middle-content-pic" src={this.props.data} onClick={this.viewDetail.bind(this, this.props.href)}/>
                <p className="news-middle-content-title">{this.props.title}</p>
                <section className="news-middle-content-fotter">
                    <span className="news-middle-content-date">2017-04-10    |    </span>
                    <a className="news-middle-content-herf" href={this.props.href}></a>
                </section>
	        </section>
        ); 
    }
}

class Industry extends Component {
    componentDidMount() {
    }
    render() {
        return (
            <section className="home">
                <HomeHeader />
                <img className="news-top-slider-bg" src="http://download.duckr.cn/vrschool/industry/slider-bg.png" />
                <section className="news-middle-box">
                    <section className="news-middle-content-box">
                        <PicText data="http://download.duckr.cn/vrschool/industry/banner1.png" title="" type="2" href="http://www.duckr.cn" />
                        <PicText data="http://download.duckr.cn/vrschool/industry/banner2.png" title="" type="2" href="http://www.baidu.com" />
                    </section>
                </section>
                <section className="news-fotter-box1">
                        <PicText data="http://download.duckr.cn/vrschool/industry/banner3.png" title=" " type="3" href="http://www.duckr.cn" />
                        <PicText data="http://download.duckr.cn/vrschool/industry/banner4.png" title="(VR)" type="3" href="http://www.baidu.com" />
                        <PicText data="http://download.duckr.cn/vrschool/industry/banner5.png" title=" " type="3" href="http://www.duckr.cn" />
                </section>
                <section className="news-fotter-box2">
                        <PicText data="http://download.duckr.cn/vrschool/industry/banner6.jpg" title="ARVR" type="3" href="http://www.duckr.cn" />
                        <PicText data="http://download.duckr.cn/vrschool/industry/banner7.png" title="VRVR" type="3" href="http://www.baidu.com" />
                        <PicText data="http://download.duckr.cn/vrschool/industry/banner8.jpg" title="VR" type="3" href="http://www.duckr.cn" />
                </section>
                <section className="news-wrapper-footer">
                    <HomeFooter />
                </section>
            </section>
        );
    }
}

export default Industry;



